export default function (src = '') {
	const srcArray = src.split('|')
	const srcObject = {
		src: '',
		width: 0,
		height: 0
	}
	if (srcArray.length > 2) {
		srcObject.src = srcArray[0]
		srcObject.width = srcArray[1] + 'px'
		srcObject.height = srcArray[2] + 'px'
	}
	return srcObject
}



