<template>
	<div class="app-main">
		<div class="app-main-container">
			<div class="preview-scroll" :class="{'config-hide':configCollapse}">
				<div class="preview-container">
					<preview class="main-preview"></preview>
				</div>
			</div>
			<configurations class="main-right" :class="{hide:configCollapse}"></configurations>
		</div>

	</div>
</template>
<script>
	import Preview from 'views/preview/index.vue'
	import Configurations from 'views/configurations/index.vue'

	export default {
		name: 'app-main',
		components: {Preview, Configurations},
		computed: {
			configCollapse () {
				return this.$store.state.layout.configCollapse
			}
		},
		data () {
			return {
				mountedCount: 0
			}
		},
		methods: {}
	}
</script>
<style lang="less">

	.app-main {

		.app-main-container {
			height: 100%;
			position: relative;
			overflow: hidden;
			.preview-scroll {
				overflow-y: auto;
				height: 100%;
				width: auto;
				margin-right: @config-width;
				transition: margin-right .5s;
				&.config-hide {
					margin-right: 0;
				}
				.preview-container {
					position: relative;
					top: 0;
					width: 100%;
					height: 100%;
					min-height: @device-height + @nav-height + 50px;
					.main-preview {
						position: absolute;
						top: 50%;
						left: 50%;
						background: #ffffff;
						transform: translateX(-50%) translateY(-50%);
					}
				}
			}

			.main-right {
				position: absolute;
				right: 0;
				top: 0;
				display: block;
				width: @config-width;
				transition: right .3s;
				&.hide {
					right: -@config-width;
				}

			}
		}

	}
</style>



