<template>
	<el-menu background-color="#545c64" text-color="#fff" active-text-color="#ffd04b" class="side-bar" @open="handleOpen" @close="handleClose" :collapse="collapse">
		<el-menu-item :disabled="false" route="/" index="0" @click.native="$router.push({name:'首页'})">
			<i class="el-icon-setting"></i> <span slot="title">Force</span>
		</el-menu-item>

		<div class="component-list">
			<div draggable="true" :key="index" v-for="(list, index) in lists" @dragover="allowDrag" @dragstart="dragstart(list)" @dragend="dragend">
				<div class="item">
					<p class="item-icon"><i :class="list.icon"></i></p>
					<p class="item-name">{{list.component.name}}</p>
				</div>
			</div>
		</div>

		<!--<el-collapse @change="handleChange" v-if="false">-->
		<!--<el-collapse-item draggable="true" :key="index" v-for="(list, index) in lists" @dragover.native="allowDrag" @dragstart.native="dragstart(list)" @dragend.native="dragend" :name="index" :title="list.name">-->
		<!--<div>-->
		<!--<div class="item" v-if="false">-->
		<!--<p><i :class="list.icon"></i></p>-->
		<!--<p>{{list.component.name}}</p>-->
		<!--</div>-->
		<!--</div>-->
		<!--</el-collapse-item>-->
		<!--</el-collapse>-->
	</el-menu>
</template>
<script>
	import { lists } from '../../util/list-arr.js'
	import createGuid from 'util/create-guid'
	import _ from 'lodash'

	export default {
		name: 'side-bar',
		props: {
			collapse: {
				type: Boolean,
				default: false
			}
		},
		data () {
			return {
				activeNames: [],
				lists: []
			}
		},
		methods: {
			handleOpen (key, keyPath) {
			},
			handleClose (key, keyPath) {

			},
			handleChange (val) {
				// console.log(val);
			},
			allowDrag (e) {
				e.preventDefault()
			},
			dragstart (list) { //拖动列表
				// 赋予guid
				list.component.guid = createGuid()
				this.$store.commit('PUSH_LIST', list.component)
			},
			dragend (e) {
				this.$store.commit('PUSH_LIST', Object.create(null))
				this.$store.commit('RESETKEY')
				// this.$api.postMessage({type: 'config', data: {config: this.$store.state.config.configurations}})
			},
			async getList () {
				this.lists = lists
			}
		},
		async created () {
			this.getList()
		}
	}
</script>
<style lang="less" scoped>
	.el-collapse {
		padding-left: 10px;
		background-color: #fff;
		.item {
			text-align: center;
			display: inline-table;
			margin: 0 5px;
		}
	}

	.component-list {
		background: white;
		color: #000;
		.item {
			cursor: pointer;
			padding-left: 24px;
			height: 50px;
			display: flex;
			align-items: center;
			justify-content: flex-start;
			border-bottom: 1px solid #e9e9e9;
			.item-icon {

			}
			.item-name {
				margin-left: 10px;
			}
		}
	}
</style>



