import store from '../store'

const status = {
	scrolled: false
}
// 展开collapse
const findCollapse = (vueComponent, options = {}) => {

	if (vueComponent.$vnode) {
		const vnode = vueComponent.$vnode
		if (vnode.componentOptions && vnode.componentOptions.tag === 'el-collapse-item') {
			if (options.type === 'locate') {
				vueComponent.$parent.activeNames.splice(0, vueComponent.$parent.activeNames.length, vueComponent.name)
				findCollapse(vueComponent.$parent, options)
			} else if (vueComponent.$parent.activeNames.indexOf(vueComponent.name) === -1) {
				// 已存在的
				vueComponent.$parent.activeNames.push(vueComponent.name)
				findCollapse(vueComponent.$parent)
			} else {
				// 不存在的
				vueComponent.$parent.activeNames.splice(0, vueComponent.$parent.activeNames.length, vueComponent.name)
				findCollapse(vueComponent.$parent)
			}
		} else {
			findCollapse(vueComponent.$parent, options)
		}
	}

}

// 向上查找
const findSubCollapse = (ref, options = {}) => {
	if (ref.$vnode && ref.$vnode.componentOptions && ref.$vnode.componentOptions.tag === 'config-form') {
		if (ref.$vnode.data.ref === options.id) {
			findCollapse(ref)
			scrollToElement(ref)
		}
	} else if (ref.$vnode && ref.$vnode.componentOptions && ref.$vnode.componentOptions.tag === 'collapse') {
		if (!ref.$children) return
		ref.$children.map(child => { // el-collapse
			if (!child.$children) return
			child.$children.map($child => { // el-collapse-item
				if ($child.$vnode.componentOptions.tag === 'el-collapse-item') {
					if ($child.name === options.id) {
						findCollapse($child, options)
						scrollToElement($child)
					} else {
						findSubCollapse($child, options)
					}
				}
			})
		})
	} else if (ref.$vnode && ref.$vnode.componentOptions && ref.$vnode.componentOptions.tag === 'el-collapse-item') {
		if (ref.name === options.id) {
			findCollapse(ref, options)
			scrollToElement(ref)
		} else {
			if (!ref.$children) return
			ref.$children.map(kid => {
				findSubCollapse(kid, options)
			})
		}
	}
}

// 滚动至所属根目录
const scrollToElement = (ref) => {
	if (typeof document !== 'undefined') {
		const $container = document.querySelector('.configurations-scroll')
		setTimeout(() => {
			$container.scrollTop = ref.$el.offsetTop - 50
		}, 500)
	}
}

const handleValidate = (ref) => {
	if (typeof(ref.validate) === 'function') {
		ref.validate((validation) => {
			if (!validation) {
				findCollapse(ref)
				store.commit('VALIDATE', false)
			}
		})
	}
}

const getId = (ref, id) => {
	findSubCollapse(ref, {
		id
	})

}

const showAllRefs = (refs = {}, options = {}) => {
	const id = options.id

	let handle = handleValidate

	if (options.type === 'locate') {
		handle = getId
	}
	Object.keys(refs).map(ref => {

		if (refs[ref]) {

			if (refs[ref] && JSON.stringify(refs[ref].$refs) !== '{}') {
				showAllRefs(refs[ref].$refs, options)
			}

			if (Array.isArray(refs[ref])) {
				refs[ref].map($ref => {
					handle($ref, id)
				})
			} else {
				handle(refs[ref], id)
			}
		}

	})
}

export default function (refs, options) {
	showAllRefs(refs, options)
}



