<template>
	<div v-loading="loading" class="configurations-outer">
		<div class="handle-bar" @click="scrollToTop">
			<span class="handle-title">配置面板</span>
			<el-button v-if="false" type="text" style="margin-right:10px;">测试</el-button>
		</div>
		<div class="configurations-scroll" @dragover="allowDrop" @drop="drop">
			<div v-show="$store.state.layout.showPageConfig" class="page-config">
				<config-form ref="form" :options="{labelWidth:'95px'}" :configs="configs"></config-form>
			</div>
			<div class="configurations">
				<collapse ref="collapse" @inCollapse="inCollapse" :componentKey="1" :tree="$store.state.config.configurations"></collapse>
			</div>
		</div>
		<div class="config-buttons">
			<el-button v-if="canBeSaved" @click="savePageDraft">保存</el-button>
			<el-button :disabled="isPreview" @click="submit">提交</el-button>
			<!--<el-button>取消</el-button>-->
		</div>
	</div>
</template>
<script>
	import Collapse from './collapse.vue'
	import ConfigForm from './form.vue'
	import decodeImageSrc from 'util/decode-image-src'
	import createGuid from 'util/create-guid'
	import _ from 'lodash'
	import toSnakeCase from 'to-snake-case'

	import locateCollapse from 'util/locate-collapse'

	export default {
		components: {
			Collapse,
			ConfigForm
		},
		data () {
			return {
				canBeSaved: false,
				loading: false,
				content: '',
				left: '',
				top: '',
				form: {},
				originalData: {},
				isPreview: this.$route.name === 'preview-page',
				notDrop: false, //表示是否置入组件栏
			}
		},
		computed: {
			configs () {
				if (this.$route.name === 'edit-page') {
					this.canBeSaved = true
				}
				// 重置验证状态
				this.$store.commit('VALIDATE', true)
				this.$api.postMessage({
					type: 'config',
					data: {config: this.$store.state.config.configurations}
				})

				return this.$store.state.config.configurations || {
					type: 'app-container',
					guid: createGuid(),
					items: [],
					components: []
				}
			}
		},
		methods: {
			scrollToTop () {
				document.querySelector('.configurations-scroll').scrollTop = 0
			},
			allowDrop (e) {
				e.preventDefault()
			},
			drop (e) {
				this.allowDrop(e)

				if (!this.notDrop) {
					const component = _.cloneDeep(this.$store.state.config.component)

					// 不能放到根目录
					if (!component.allowRoot) {
						return this.$message.error('不能放到根目录')
					}

					const len = Object.keys(component).length

					if (len === 0) return //不是来自列表栏的组件拖动
					const configurations = {...this.$store.state.config.configurations}
					configurations.components.push(component)
					// this.$store.commit('CHANGE_CONFIG', configurations)
					this.$message('插入成功')
					try {
						this.$children[1].activeNames = [component.guid]
					} catch (e) {
						console.log(e)
					}
				}

				this.notDrop = false

			},
			inCollapse () {
				this.notDrop = true
			},
			validateAll () {
				try {
					this.$refs.form.validate()
					this.$refs.collapse.validate()
				} catch (e) {
					console.log(e)
				}

				return this.$store.state.config.validation
			},
			locateAll (id) {
				// 定位
				try {
					this.$refs.form.locate(id)
					this.$refs.collapse.locate(id)
				} catch (e) {
					console.log(e)
				}

			},
			validate () {
				// 检查锚点数
				let result = true
				let warning = ''
				const status = {
					anchors: {
						// Array.isArray(component.components) && component.components
						eval: 'Array.isArray(component.components) && component.components.length < 2',
						validation: true,
						warning: '锚点数必须不少于2个！'
					}
				}
				const cfg = this.$store.state.config.configurations
				if (cfg.components) {
					cfg.components.map(component => {
						Object.keys(status).map(key => {
							if (component.type === key) {
								if (eval(status[key].eval)) {
									status[key].validation = false
								}
							}
						})

					})
				}
				for (const key in status) {
					if (!status[key].validation) {
						result = false
						warning = status[key].warning
						break
					}
				}
				if (!result) {
					this.$message.warning(warning)
				}
				return result
			},
			submit () {
				if (!this.validateAll()) return this.$message.warning('请先检查输入！')
				if (!this.validate()) return
				const configs = this.configs
				const options = {
					title: '',
					bg_color: '',
					title_config: '',
					share_title: '',
					share_image: '',
					share_link: '',
					show_start_time: 0,
					show_end_time: 0,
					status: 0,
					share_text: 0,
					content: this.content,
					config_data: JSON.stringify(this.$store.state.config.configurations)
				}
				configs.items.map(item => {
					const rp = toSnakeCase(item.realProp)
					if (rp === 'share_image') {
						const imageObject = decodeImageSrc(item.value)
						options['share_image'] = imageObject.src
					} else if (rp === 'title_config') {
						options[rp] = item.value.join(',')
					} else if (/^show_.+_time$/ig.test(rp) && item.value) {
						if (_.isDate(item.value)) {
							// Date 类型
							options[rp] = ~~(item.value.getTime() / 1000)
						} else if (_.isString(item.value)) {
							// 字符串类型
							options[rp] = Date.parse(item.value) / 1000
						} else if (_.isNumber(item.value)) {
							// 数字类型
							options[rp] = item.value
						} else {
							// 其他类型
							options[rp] = 0
						}

					} else {
						options[rp] = item.value
					}
				})

				//
				if (this.$route.name === 'edit-page' && this.$route.params.id) {
					// 编辑
					options.id = ~~(this.$route.params.id)
					this.$api.putPageInfo(options).then(res => {
						if (res.error_code === 0) {
							this.$message.success('提交成功！')
							// 回到首页
							this.$router.push({
								name: '首页'
							})
						} else {
							this.$message.warning(res.error_description)
						}
					}).catch(err => {
						console.log(err)
					})
				} else {
					// 新建和复制
					this.$api.postPageInfo(options).then(res => {
						if (res.error_code === 0) {
							this.$message.success('提交成功！')
							// 回到首页
							this.$router.push({
								name: '首页'
							})
						} else {
							this.$message.warning(res.error_description)
						}
					}).catch(err => {
						console.log(err)
					})
				}

			},
			savePageDraft () {
				// if (!this.validateAll()) return this.$message.warning('请先检查输入！') // 保存不需要校验
				// if (!this.validate()) return
				const options = {
					id: 0,
					config_data: JSON.stringify(this.$store.state.config.configurations)
				}

				//
				if (this.$route.name === 'edit-page' && this.$route.params.id) {
					// 编辑
					options.id = ~~(this.$route.params.id)
					this.$api.postPageDraft(options).then(res => {
						if (res.error_code === 0) {
							this.$message.success('保存成功！')
							this.getConfig()
							// 回到首页
						} else {
							this.$message.warning(res.error_description)
						}
					}).catch(err => {
						console.log(err)
					})
				}

			},
			configsFallback (configs) {
				const washing = (config) => {
					if (config.items && config.items.length) {
						config.items = config.items.map(item => {

							const _item = {...item}

							if (_item.name.indexOf('date-picker') > -1) {
								if (_item.value && typeof( _item.value) === 'string') {
									_item.value = new Date(_item.value)
								}
							}

							// 去掉验证开关
							delete _item.validation

							delete _item.items

							return _item
						})
					}
					if (config.components && config.components.length) {
						config.components = config.components.map(washing)
					}

					return {...config}
				}
				return washing(configs)
			},
			async getConfig () {
				// 请求数据
				const pageName = this.$route.name
				if (pageName === 'edit-page' || pageName === 'copy-page') {
					this.loading = true
					const configs = await this.$api.getPageInfo(this.$route.params.id).then(res => {
						if (res.error_code === 0) {
							this.originalData = res.data
							const data = JSON.parse(res.data.config_data)
							const link = data.share_link || res.data.web_url

							if (link) {
								// 分享链接
								data.items && data.items.map(item => {
									const prop = item.realProp || item.prop
									if (toSnakeCase(prop) === 'share_link' && !item.value) {
										item.value = link
									}
								})
							}

							// 复制的时候时间置空
							if (this.$route.name === 'copy-page') {
								data.items && data.items.map(item => {
									const prop = item.realProp || item.prop
									if (/show_.*_time/.test(toSnakeCase(prop)) && item.value) {
										item.value = ''
									}
								})
							}

							return data
						}

						return _.cloneDeep(this.configs)
					}).catch(e => {console.log(e)})
					this.$store.commit('CHANGE_CONFIG', this.configsFallback(configs))
					this.loading = false
				} else {
					const guid = createGuid()
					this.$store.commit('CHANGE_CONFIG', {...JSON.parse(JSON.stringify(require('api/json/page.json'))), guid})
				}
			},

		},
		created () {
			window.addEventListener('message', (event) => {
				const data = event.data
				if (event.source.location.origin === window.location.origin) {
					const message = data.data || {}
					if (data.type === 'content') {

						this.content = message.content
						// console.log('%c[configurations]: 配置数据来自' + event.source.location.href, 'color: #ff5577;')
						// this.$store.commit('CHANGE_CONFIG', config)
					} else if (data.type === 'action') {
						if (message.action.mounted) {
							this.$api.postMessage({
								type: 'config',
								data: {config: this.$store.state.config.configurations}
							})
						}
					} else if (data.type === 'locate') {
						// 定位collapse
						const location = message.location
						this.locateAll(location.id)
					}
				}

			}, false)

		},
		mounted () {
			this.getConfig()
		}
	}
</script>
<style lang="less">
	.configurations-outer {
		height: 100%;
		border-left: 1px solid #e6e6e6;
		box-shadow: @box-shadow-left;
		background: white;
		position: relative;
		font-size: 12px;
		.handle-bar {
			position: absolute;
			top: 0;
			height: 50px;
			width: 100%;
			display: flex;
			justify-content: flex-start;
			align-items: center;
			background: #ffffff;
			border-bottom: 1px solid #e6e6e6;
			box-shadow: @box-shadow-bottom;
			z-index: 9999;
			.handle-title {
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
			}
			.handle-back {
				margin-left: 10px;
			}
		}
		.configurations-scroll {
			padding: 50px 0;
			height: 100%;
			box-sizing: border-box;
			overflow-x: hidden;
			overflow-y: auto;
			.configurations {
				// 重置样式
				.el-collapse-item:not(.is-active) {
					.el-collapse-item__header {
						border-bottom: 1px solid #ebeef5;
						.el-collapse-item__arrow {
							transform: rotate(0);
						}
					}
				}
			}
		}

		.config-buttons {
			background: #ffffff;
			position: absolute;
			bottom: 0;
			display: flex;
			justify-content: space-around;
			width: 100%;
			padding: 10px 0;
			border-top: 1px solid #e6e6e6;
			box-shadow: @box-shadow-top;
			z-index: 9999;
		}
	}

	.generator {
		fieldset {
			border: none;
			padding: 0;
			margin: 0;
		}
		.field-checklist .wrapper {
			width: 100%;
			.list-row {
				margin: 5px 0;
			}
		}
	}

	.common-bar {
		width: 0;
		height: 0;
	}

	.page-config {
		padding: 20px 20px 0 0;
	}
</style>



