<script>
	import createGuid from '../../util/create-guid'
	import _ from 'lodash'
	import locateCollapse from 'util/locate-collapse'

	export default {
		name: 'config-form',
		props: {
			configs: {
				type: Object,
				required: true,
				default () {
					return {
						type: 'app-container',
						items: []
					}
				}
			},
			options: {
				type: Object,
				default: () => ({})
			}
		},
		computed: {
			form () {
				const _form = {}
				this.configs.items &&
				this.configs.items.map(item => {
					_form[item.prop] = item.value
				})
				return _form
			},
			rules () {
				// 验证规则
				const _rules = {}
				this.configs.items &&
				this.configs.items.map(item => {
					const prop = createGuid() // 这里把 _rules 的 prop guid
					item.prop = item.prop || ''
					const propArray = item.prop.split('|')

					if (propArray.length < 2) {
						item.realProp = item.prop
						item.prop = (item.prop + '|' + prop)
						item.guid = prop
					} else {
						item.realProp = propArray[0]
					}

					_rules[item.prop] = [
						// 是否必填
						{
							required: item.required || false,
							message: `请输入${item.label}`,
							trigger: 'blur'
						},

					]

					if (item.type) {
						_rules[item.prop].push(// 参数类型
							{
								type: item.type,
								message: '请检查输入类型',
								trigger: 'blur'
							})
					}
				})
				return _rules
			}
		},
		methods: {
			validate () {
				locateCollapse(this.$refs, {
					type: 'validate'
				})
			},
			locate (id) {
				locateCollapse(this.$refs, {
					type: 'locate',
					id
				})
			},
		},
		render (h) {
			const circle = (item, items) => {
				// 验证通过
				item.validation = true
				item.props.placeholder = `请填写${item.label}`

				const r = (item) => {
					// 特殊处理
					// select
					if (item.name === 'select' && item.values) {
						item.props.placeholder = `请选择${item.label}`
						item.items = item.values.map(value => {
							const option = {
								name: 'option',
								noFormItem: true
							}
							if (typeof value === 'string') {
								option.value = value
								option.props = {
									label: value
								}
							} else if (value.hasOwnProperty('label') && value.hasOwnProperty('value')) {
								option.value = value.value
								option.props = {
									label: value.label
								}
							}
							return option
						})
					} else if (item.name === 'radio-group' && item.values) {
						// 单选组
						item.items = item.values.map(value => {
							const radio = {
								name: 'radio',
								noFormItem: true
							}
							if (typeof value === 'string') {
								radio.value = value
								radio.props = {
									label: value
								}
								radio.slot = value
							} else if (value.hasOwnProperty('label') && value.hasOwnProperty('value')) {
								radio.value = value.value
								radio.props = {
									label: value.value
								}
								radio.slot = value.label

							}
							return radio
						})
					} else if (item.name === 'checkbox-group' && item.values) {
						// 多选组
						item.items = item.values.map(value => {
							const checkbox = {
								name: 'checkbox',
								noFormItem: true
							}
							if (typeof value === 'string') {
								checkbox.value = value
								checkbox.props = {
									label: value
								}
								checkbox.slot = value
							} else if (value.hasOwnProperty('label') && value.hasOwnProperty('value')) {
								checkbox.value = value.value
								checkbox.props = {
									label: value.value
								}
								checkbox.slot = value.label

							}
							return checkbox
						})
					} else if (item.name === 'image-picker') {
						// 图片选择器
						item.noEl = true
					} else if (item.name === 'app-route-picker') {
						// 路由选择器
						item.noEl = true
					}

					// 名字的特殊处理
					if ((this.configs.name === '锚点' && item.label === '锚点名称')||(this.configs.name === '优惠券' && item.label.toLowerCase() === '券id')) {
						this.$emit('showName', item.value)
					}

					return h((item.noEl ? '' : 'el-') + item.name, {
						on: {
							input: (value) => {
								item.value = value
								// this.$api.postMessage({type: 'config', data: {config: this.$store.state.config.configurations}})
							}
						},
						props: {
							...item.props,
							value: item.value,
						},
					}, (item.items && item.items.length) ? item.items.map(i => circle(i)) : item.slot)
				}

				if (item.noFormItem) {
					return r(item)
				} else {
					// 简单依赖处理，暂时支持同级依赖
					if (item.dependencies && item.dependencies.length) {
						item.dependencies.map(dependency => {

							if (dependency.type === 'indexOf') {
								// 数组或者字符串
								items.map(it => {
									if (it.realProp === dependency.prop && it.value.indexOf(dependency.value) === -1) {
										item.validation = false
									}
								})
							}
						})
						if (!item.validation) return h('')
					}

					return h('el-form-item', {
						props: {
							label: item.label,
							prop: item.prop
						}
					}, [r(item)])
				}

			}
			return h('el-form', {
				props: {
					labelWidth: this.options.labelWidth || '80px',
					model: this.form,
					rules: this.rules,
					size: this.options.size || 'mini'
				},
				domProps: {
					draggable: false
				},
				nativeOn: {
					dragstart (event) {
						event.preventDefault()
						event.stopPropagation()
					}
				},
				style: {
					'-webkit-user-drag': 'element'
				},
				ref: this.configs.guid || createGuid(),
			}, this.configs.items ? this.configs.items.map(item => circle(item, this.configs.items)) : [])
		},
		created () {

		}
	}
</script>



