/**
 * 生成一个8位的随机字母和数字的组合
 * @returns {guid} | String
 */


// guid 缓存
const guids = []

const guidFactory = () => {
	const chars = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'
	const len = chars.length

	let str = ''
	for (let i = 0; i < 8; i++) {
		const t = Math.floor(Math.random() * len)
		str += chars[t]
	}
	if (guids.indexOf(str) > -1) {
		return guidFactory()
	}
	guids.push(str)
	return str
}

export default function () {

	return guidFactory()

}



