<template>
	<el-card class="preview-outer" :body-style="{padding:0}" :class="{wrapping:$store.state.layout.dragging}">
		<div class="preview-top" v-if="false">
			<el-carousel :autoplay="false" indicator-position="none" type="card" height="50px">
				<el-carousel-item v-for="phone in phones" :key="phone">
					<h3>{{ phone }}</h3>
				</el-carousel-item>
			</el-carousel>
		</div>
		<div v-if="false" class="preview-left">
			<ul>
				<li>
					<el-button plain>左</el-button>
				</li>
				<li>
					<el-button plain>边</el-button>
				</li>
				<li>
					<el-button plain>工</el-button>
				</li>
				<li>
					<el-button plain>具</el-button>
				</li>
				<li>
					<el-button plain>栏</el-button>
				</li>
			</ul>
		</div>
		<div class="preview-right" v-if="true">
			<ul>
				<li>
					<el-button @click="edit">{{previewing ? '预览模式' : '编辑模式'}}</el-button>
				</li>
				<li>
					<el-button :type="$store.state.layout.showPageConfig?'success':''" @click="showPageConfig">全局设置</el-button>
				</li>
			</ul>
		</div>
		<div class="preview-title">
			<div class="title-back" v-if="false">
				<el-button class="button-back" type="text" icon="el-icon-arrow-left">返回</el-button>
			</div>
			<div class="title-text">{{title}}</div>
			<div class="title-share" v-if="showShare" @click="showShareMenu">

				<el-dropdown :hide-on-click="false">
					<el-button class="button-share" type="text" icon="el-icon-share"></el-button>
					<el-dropdown-menu slot="dropdown">
						<el-dropdown-item>微信</el-dropdown-item>
						<el-dropdown-item>朋友圈</el-dropdown-item>
						<el-dropdown-item>微博</el-dropdown-item>
						<el-dropdown-item>QQ空间</el-dropdown-item>
					</el-dropdown-menu>
				</el-dropdown>
			</div>
			<div class="title-share" v-if="true" @click="goBackHome">
				<el-button class="button-share" type="text" icon="el-icon-refresh"></el-button>
			</div>
		</div>

		<iframe ref="preview" class="preview-wrapper" :src="previewUrl" frameborder="0"></iframe>
		<div class="preview-bottom" v-if="false">
			<el-button-group>
				<el-button>底</el-button>
				<el-button>部</el-button>
				<el-button>工</el-button>
				<el-button>具</el-button>
				<el-button>栏</el-button>
			</el-button-group>
		</div>
	</el-card>
</template>
<script>
	export default {
		name: 'preview',
		components: {},
		data () {
			return {
				previewUrl: 'preview/index.html?_=' + new Date().getTime(),
				previewing: false,
				wrapping: false,
				phones: [
					'iPhone X',
					'iPhone 8',
					'iPhone 7',
					'SamSung s8'
				]
			}
		},
		computed: {
			title () {
				return this.$store.getters.pageTitle
			},
			showShare () {
				return this.$store.getters.showShare
			}
		},
		methods: {
			showShareMenu () {

			},
			goBackHome () {
				this.previewUrl = 'preview/index.html?_=' + new Date().getTime()
				this.previewing = false
			},
			log (event) {
				console.log(event)
			},
			trigger () {
				this.$store.commit('HANDLE_DRAGGING')
				// this.$api.postMessage({type: 'config', data: {config: this.$store.state.config.configurations}})
			},
			showPageConfig () {
				this.$store.commit('HANDLE_PAGE_CONFIG')
			},
			edit () {
				this.previewing = !this.previewing
				this.$api.postMessage({
					type: 'action',
					data: {action: {touch: !!this.previewing}}
				})
			}
		}
	}
</script>
<style lang="less">

	.preview-outer {
		position: relative;
		overflow: visible;
		&.wrapping {
			box-shadow: 0 1px 10px 0 rgb(0, 0, 0) inset;
			&:after {
				position: absolute;
				left: 0;
				top: 0;
				content: '';
				width: 100%;
				height: 100%;
				background-color: transparent;
			}

		}
		.preview-top {
			position: absolute;
			top: 0;
			width: 100%;
			transform: translateY(-110%);
			.el-carousel__item h3 {
				margin: 0;
				text-align: center;
				color: #475669;
				font-size: 14px;
				opacity: 0.75;
				line-height: 50px;
			}

			.el-carousel__item:nth-child(2n) {
				background-color: #99a9bf;
			}

			.el-carousel__item:nth-child(2n+1) {
				background-color: #d3dce6;
			}
		}
		.preview-left {
			position: absolute;
			right: 100%;
			transform: translateX(-10%);
			ul {
				li {
					margin-top: 10px;
				}
			}
		}
		.preview-right {
			position: absolute;
			right: 0;
			transform: translateX(110%);
			ul {
				padding: 0;
				li {
					margin-top: 10px;
				}
			}
		}
		.preview-title {
			position: relative;
			height: 56px;
			border-bottom: 1px solid #e6e6e6;
			display: flex;
			align-items: center;
			justify-content: flex-end;
			.title-back {
				width: 20%;
				display: flex;
				align-items: center;
				.button-back {
					margin-left: 10px;
					font-size: 16px;
					color: rgba(0, 0, 0, .5);
				}
			}
			.title-text {
				position: absolute;
				top: 50%;
				left: 50%;
				width: 50%;
				font-size: 16px;
				transform: translate(-50%, -50%);
				text-align: center;
			}
			.title-share {
				width: 12%;
				display: flex;
				justify-content: center;
				.button-share {
					text-align: center;
					width: 20px;
					font-size: 20px;
					color: rgba(0, 0, 0, .5);

				}
			}
		}
		.preview-wrapper {
			position: relative;
			width: @device-width;
			height: @device-height;

		}
		.preview-bottom {
			position: absolute;
			top: 100%;
			width: 100%;
			transform: translateY(20%);
			display: flex;
			justify-content: center;
		}
	}

</style>



