<template>
  <el-menu class="nav-bar" mode="horizontal">
    <hamburger v-if="false" class="hamburger-side" @trigger="sideTrigger" :rows="3"
               :active="!$store.state.layout.sideCollapse"></hamburger>
    <div class="placeholder"></div>

    <hamburger class="hamburger-config" @trigger="trigger" :rows="2"
               :active="!$store.state.layout.configCollapse"></hamburger>
  </el-menu>
</template>
<script>
  import Hamburger from 'components/hamburger/index.vue'

  export default {
    name: 'nav-bar',
    components: {
      Hamburger
    },
    data () {
      return {}
    },
    methods: {
      sideTrigger () {
        this.$store.commit('HANDLE_SIDE_COLLAPSE')
      }, trigger () {
        this.$store.commit('HANDLE_CONFIG_COLLAPSE')
      }
    }
  }
</script>
<style lang="less">

  .nav-bar {
    height: @nav-height;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: @min-width;
    &:before, &:after {
      content: none;
    }
    .placeholder {
      width: 1px;
      height: 1px;
    }
    .hamburger-side, .hamburger-config {
      height: @nav-height;
      width: @nav-height;
      display: flex;
      justify-content: center;
      align-items: center;
      outline: none;
    }
    .hamburger-side {
    }
    .hamburger-config {

    }
  }
</style>



