<template>
	<el-collapse v-model="activeNames" :style="{'border-left': '2px solid ' + colors[betterTree.id?betterTree.id.split('-').length*4%(colors.length-1):0],'border-right':'1px solid #ebeef5'}">
		<el-collapse-item v-for="(component, index) in betterTree.components" :key="component.guid" draggable="true" @dragstart.native="dragstart($event, index, component)" @dragenter.native="dragenter(component, index)" @dragend.native="dragend" @dragover.native="allowDrop" @drop.native="drop($event, index)" :name="component.guid" @click.native.stop="findRenderedComponent(component.guid)">
			<template slot="title">
				<div class="text-indent-10">
					<span class="collapse-title">{{component.name || '配置加载中...'}}</span>
					<span v-if="component.displayName">[{{component.displayName}}]</span>
					<!--<span>{{activeNames}}</span>-->
					<template v-if="component.name">
						<el-button v-if="showDelete(component)" @click.stop="del(index)" style="margin-left:10px;color: black;" type="text" icon="el-icon-delete"></el-button>
						<el-button v-if="false" style="margin-left:10px;color: black;" type="text" icon="el-icon-rank"></el-button>
						<!--<el-button @click.stop="add()" style="margin-left:10px;color: black;" type="text" icon="el-icon-plus" v-if="component.components"></el-button>-->
					</template>
				</div>
			</template>
			<div class="padding" @dragover="allowDrop">
				<config-form @showName="setName(component, $event)" :ref="component.guid || createGuid()" :configs="component" :options="component.options"></config-form>
				<collapse :ref="createGuid()" :componentKey="(componentKey + 1)" v-if="component.components" :tree="component"></collapse>
			</div>
		</el-collapse-item>
	</el-collapse>
</template>
<script>
	import ConfigForm from './form.vue'
	import _ from 'lodash'
	import createGuid from 'util/create-guid'
	import locateCollapse from 'util/locate-collapse'

	export default {
		name: 'collapse',
		components: {
			ConfigForm
		},
		props: {
			componentKey: {
				type: Number, // 标识当前节点
			},
			tree: {
				type: Object,
				required: true,
				default: () => ({})
			},
		},
		computed: {
			betterTree () {
				// 洗一下树据
				const betterTree = {...this.tree}

				if (Array.isArray(betterTree.components) && betterTree.components.length) {
					const items = betterTree.items || []
					betterTree.components = betterTree.components.filter(component => {
						let validation = true

						if (Array.isArray(component.dependencies) && component.dependencies.length && items.length) {
							component.dependencies.map(dependency => {
								// 依赖的遍历
								items.map(item => {
									const prop = item.realProp || item.prop
									if (prop === dependency.prop) {
										// 正则
										if (dependency.regex) {
											const regex = new RegExp(dependency.regex, 'ig')
											if (!regex.test(item.value)) {
												validation = false
											}
										}
									}
								})
							})
						}

						return validation
					}).map(component => {
						if (!component.guid) {
							component.guid = createGuid()
						}
						return component
					})
				}
				return betterTree

			}
		},
		data () {
			return {
				fromCollapse: false, //标记是否来自右侧组件栏 默认：false
				fromKey: '',
				item: {},

				activeNames: ['1-0'], //这里不能用computed 受限饿了么的组件设计

				colors: [
					// 最佳视觉色彩 by Material Design
					'rgb(244,67,54)',
					'rgb(233,30,99)',
					'rgb(156,39,176)',
					'rgb(103,58,183)',
					'rgb(63,81,181)',
					'rgb(33,150,243)',
					'rgb(3,169,244)',
					'rgb(0,188,212)',
					'rgb(0,150,136)',
					'rgb(76,175,80)',
					'rgb(139,195,74)',
					'rgb(205,220,57)',
					'rgb(255,235,59)',
					'rgb(255,193,7)',
					'rgb(255,152,0)',
					'rgb(255,87,34)',
					'rgb(121,85,72)',
					'rgb(158,158,158)',
					'rgb(96,125,139)'
				],

				handleindex: 0, //记录操作的是哪个唯一的组件
			}
		},
		methods: {
			setName (component, name) {
				component.displayName = name
			},
			showDelete (component) {
				return !/(优惠券$|图片)/.test(component.name)
			},
			validate () {
				locateCollapse(this.$refs, {
					type: 'validate'
				})
			},
			locate (id) {
				locateCollapse(this.$refs, {
					type: 'locate',
					id
				})
			},
			add () {

			},
			findRenderedComponent (guid) {
				this.$api.postMessage({
					type: 'locate',
					data: {
						location: {
							id: guid
						}
					}
				})
			},
			createGuid () {
				return createGuid()
			},
			del (index) {
				this.$confirm('此操作将立刻删除该组件, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => { //确定删除
					// const configurations = {...this.$store.state.config.configurations}
					const configurations = this.tree
					configurations.components.splice(index, 1)

					this.$message({
						type: 'success',
						message: '删除成功!'
					})

				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消删除'
					})
				})
			},
			allowDrop (e) {
				e.preventDefault()
			},
			dragstart (e, index, item) { //开始拖拽
				this.handleindex = index
				this.item = item //保存移动的对象
				this.fromCollapse = true //表示来自右侧组件栏
			},
			dragenter (item, index) {
				if (this.fromCollapse) return //自己排序的就不展开了

				// let arr = [...this.activeNames]
				// arr.push(this.componentKey + '-' + index)
				// arr = [...new Set(arr)] //去重

				// this.activeNames = arr
				if (item.accepts && item.accepts.indexOf(this.$store.state.config.component.type) > -1) {
					this.activeNames = [item.guid] //只展开一个
				}
			},
			dragend (e) { //松手
				this.fromCollapse = false
				this.item = Object.create(null)
			},
			drop (e, index) { //来自右侧组件栏内部放置 index组件放置的位置
				e.stopPropagation() // 阻止冒泡

				const putOutside = (component) => {
					if (this.fromCollapse) return
					// 放在根目录
					// 不能放到根目录
					if (!component.allowRoot) {
						return this.$message.error('不能放到根目录')
					}
					const configurations = {...this.$store.state.config.configurations}
					configurations.components.push(component)

					this.$nextTick(() => {
						try {
							locateCollapse(this.$refs, {
								type: 'locate',
								id: component.guid
							})
						} catch (e) {
							console.log(e)
						}
					})
				}

				this.$store.commit('MARKKEY', this.componentKey) //记录最深的层级，传输，避免内外层重复插入

				this.$emit('inCollapse')
				this.allowDrop(e)

				const startIndex = parseInt(this.handleindex) //组件初始的位置
				const configurations = this.tree

				if (this.fromCollapse) { //自身拖动，用于排序
					configurations.components.splice(this.handleindex, 1)
					configurations.components.splice(index, 0, this.item)

					// this.$store.commit('CHANGE_CONFIG', configurations)
					this.$message('排序成功')
					return
				}

				// 插入内嵌组件逻辑
				const component = _.cloneDeep(this.$store.state.config.component)
				const accepts = configurations.components[index].accepts
				if (!accepts) return putOutside(component)
				const t = accepts.find(n => n === component.type) //查找插入的组件是否属于accepts
				if (!t) return putOutside(component)

				if (this.$store.state.config.componentKey > this.componentKey) return

				configurations.components[index].components.push(component)

				this.$message('插入内嵌组件成功')

				this.$store.commit('CHANGE_CONFIG', {...this.$store.state.config.configurations})

				this.$nextTick(() => {
					try {
						locateCollapse(this.$refs, {
							type: 'locate',
							id: component.guid
						})
					} catch (e) {
						console.log(e)
					}
				})

			}
		}
	}
</script>
<style>
	.text-indent-10 {
		text-indent: 10px;
	}

	.padding {
		padding: 10px 5px 0;
	}

	.collapse-title {
		font-weight: bold;
		font-size: 18px;
	}
</style>



