<template>
	<div class="base-wrapper" :class="{'hide-side':sideCollapse}">
		<side-bar :collapse="sideCollapse" class="side-container"></side-bar>
		<div class="main-container">
			<app-main class="main"></app-main>
			<nav-bar v-if="false" class="nav-container"></nav-bar>
		</div>
	</div>
</template>
<script>
	import { NavBar, AppMain, SideBar } from 'views/base'

	export default {
		name: 'root',
		components: {
			NavBar, AppMain, SideBar
		},
		computed: {
			sideCollapse () {
				return this.$store.state.layout.sideCollapse
			}
		},
		data () {
			return {}
		}
	}
</script>
<style lang="less">
	@nav-height: 0;
	.base-wrapper {
		width: 100%;
		&.hide-side {
			.side-container {
				width: @side-width-collapse;
			}
			.main-container {
				left: @side-width-collapse;

			}
		}
		.side-container {
			position: fixed;
			left: 0;
			top: 0;
			bottom: 0;
			width: @side-width;
			color: #ffffff;
			z-index: 11;
			transition: width .3s;
			border: none;
		}

		.main-container {
			position: fixed;
			top: 0;
			left: @side-width;
			right: 0;
			bottom: 0;
			height: 100%;
			overflow-y: hidden;
			transition: left .3s;
			z-index: 10;
			// border-left: 1px solid #e6e6e6;
			.nav-container {
				width: 100%;
				background: #cdd7e2;
			}
			.main {
				position: absolute;
				top: @nav-height;
				background: #f8f8f8;
				left: 0;
				bottom: 0;
				width: 100%;
				min-width: @min-width;
			}
		}
	}
</style>



